import { Bounce, toast } from "react-toastify";

const TOAST_SETTING = {
  position: "bottom-center",
  autoClose: 5000,
  hideProgressBar: true,
  closeOnClick: true,
  pauseOnHover: true,
  draggable: true,
  progress: undefined,
  theme: "dark",
  transition: Bounce,
};

/**
 * @description -         .
 * @param toastMessage -    ,   .
 * @param situation 
 */
const callToastNotify = (
  toastMessage = "default Message",
  situation = "success"
) => {
  switch (situation) {
    case "success":
      toast.success(toastMessage, TOAST_SETTING);
      break;
    case "error":
      toast.error(toastMessage, TOAST_SETTING);
      break;
    default:
      toast.error("success error situation .", TOAST_SETTING);
  }
};

export { callToastNotify };
