import React from "react";
import CardStyle from "./Card.style";
import ImageCard from "./ImageCard";
import { CARD_COLOR } from "../../../../constant/list";

/**
 *
 * @description Card   props 
 * @property {string} type -    . "color"  "image"
 * @property {number} cardColorChecks -    
 * @property {number} cardImageChecks -    
 * @property {function} handleCardColorCheck -       
 * @property {function} handleCardImageCheck -       
 * @property {Array<string>} cardImage -     URL 
 */
const CardList = ({
  type,
  cardColorChecks,
  cardImageChecks,
  handleCardColorCheck,
  handleCardImageCheck,
  cardImage,
}) => {
  return (
    <>
      {type === "color"
        ? CARD_COLOR.map((color, index) => (
            <CardStyle
              key={index}
              value={color}
              color={color}
              onClick={() => handleCardColorCheck(index)}
            >
              {cardColorChecks === index && (
                <img
                  className="check-image"
                  src="/assets/post-to/Enabled.svg"
                  alt=" "
                />
              )}
            </CardStyle>
          ))
        : cardImage.map((imageUrl, index) => (
            <ImageCard
              key={index}
              imageUrl={imageUrl}
              handleCardImageCheck={handleCardImageCheck}
              cardImageChecks={cardImageChecks}
              index={index}
            />
          ))}
    </>
  );
};

export default CardList;
