/**
 * @description -   
 * @param title -    ,   .
 * @param description  
 * @param imageUrl  
 */
export function shareKakao() {
  const API_KEY = process.env.REACT_APP_KAKAO_KEY;
  const { Kakao } = window;
  const realUrl = "https://itsbasic-c93d6.web.app/"; //  url 
  const resultUrl = window.location.href;

  Kakao.cleanup();
  Kakao.init(API_KEY);

  const showShare = () => {
    Kakao.Share.sendDefault({
      objectType: "feed",
      content: {
        title: "",
        description: " ,      ",
        imageUrl:
          "https://raw.githubusercontent.com/innerstella/itsBasic/188af8aa214f784250f20f079282e8ce54017632/public/assets/common/kakao_share.png",
        link: {
          mobileWebUrl: realUrl,
        },
      },
      buttons: [
        {
          title: "  ",
          link: {
            mobileWebUrl: resultUrl,
          },
        },
      ],
    });
  };
  showShare();
}
