import { useState } from "react";
import CardStyle from "./Card.style";
import SkeletonUI from "../../../../components/skeleton-ui/SkeletonUI";

/**
 *
 * @description ImageCard   props 
 * @property {string} imageUrl -  url
 * @property {number} handleCardImageCheck -       
 * @property {number} cardImageChecks -    
 * @property {function} index -   
 * @property {function} index -   
 */
const ImageCard = ({
  imageUrl,
  handleCardImageCheck,
  cardImageChecks,
  index,
}) => {
  const [imageLoaded, setImageLoaded] = useState(false);

  return (
    <CardStyle onClick={() => handleCardImageCheck(index)}>
      <img
        className="img"
        src={imageUrl}
        alt=" "
        onLoad={() => setImageLoaded(true)}
        onError={() => setImageLoaded(true)}
      />
      {!imageLoaded && (
        <SkeletonUI
          width="16.8rem"
          height="16.8rem"
          mWidth="15.4rem"
          mWeight="15.4rem"
          radius="1.6rem"
        />
      )}
      {cardImageChecks === index && imageLoaded && (
        <>
          <img
            className="check-image"
            src="/assets/post-to/Enabled.svg"
            alt=" "
          />
          {imageLoaded && <div className="select-opacity" />}
        </>
      )}
    </CardStyle>
  );
};

export default ImageCard;
