import { useEffect, useState } from "react";
import RollingMessageModal from "../RollingMessageModal/RollingMessageModal.jsx";
import Relationship from "./CardRelationship";
import fetchData from "../Utils/API.js";
import TrashButton from "./TrashButton";
import { formatDate } from "../../../../utils/formatDate.js";
import { useParams, useNavigate } from "react-router-dom";
import { useInView } from "react-intersection-observer";
import * as S from "./PostCardItem.style";
import SkeletonUI from "../../../../components/skeleton-ui/SkeletonUI.jsx";

export function PostCardItem({ amountDataCount, setAmountDataCount }) {
  const [ref, inView] = useInView();
  const [page, setPage] = useState(8);
  const [cardData, setCardData] = useState([]);
  const { recipientId } = useParams();
  const [modalCardData, setModalCardData] = useState({});
  const [isMessageOpen, setIsMessageOpen] = useState(false);
  const [cardLoaded, setCardLoaded] = useState(false);

  const dataUrl = `recipients/${recipientId}/messages/?limit=9&offset=${page}`;
  const isPageOwner = localStorage.getItem(`${recipientId}-Post`) === "Owner";

  /**
   * Post      fetching 
   */
  async function fetchFirstData() {
    const firstData = await fetchData(`recipients/${recipientId}/messages/`);
    setAmountDataCount(firstData.count);
    const paperData = firstData.results;
    setCardData(paperData);
    setCardLoaded(true);
  }

  /**
   *     offset 9  cardData     
   */
  async function fetchOverscrollData() {
    setPage((prev) => prev + 9);
    const jsonData = await fetchData(dataUrl);
    const paperData = jsonData.results;
    setCardData([...cardData, ...paperData]);
  }

  /**
   *         fetching 
   */
  async function deleteFetchData() {
    const jsonData = await fetchData(
      `recipients/${recipientId}/messages/?limit=${cardData.length}`
    );

    const paperData = jsonData.results;
    setCardData(paperData);
  }
  useEffect(() => {
    fetchFirstData();
  }, []);

  /**
   * S.ContentEndPoint useRef      inView true   false (   )
   * true  fetchData   cardData 9  .
   *             .
   */
  useEffect(() => {
    if (inView) {
      if (cardData.length > 5 && cardData.length < amountDataCount) {
        fetchOverscrollData();
      }
    }
  }, [inView]);

  function handleMessageModalOpen(el) {
    setModalCardData(el);
    setIsMessageOpen(true);
  }
  const currentURL = window.location.href;
  const navigate = useNavigate();

  /**
   *        delete  deleteFetchData       fetching
   *       offset    
   * setAmountDataCount useParams  id          -1 
   *   /post/id/edit  
   */
  function onDeleteItem(e) {
    e.stopPropagation();

    const deleteUrl = `messages/${e.target.id}/`;
    if (window.confirm("   ?")) {
      fetchData(deleteUrl, { method: "DELETE" });

      setTimeout(function () {
        deleteFetchData();
        setPage(cardData.length);
        setAmountDataCount((prev) => prev - 1);
        localStorage.removeItem(`${e.target.id}-Message`);
        navigate(`/post/${recipientId}/edit`);
      }, 300);
    }
  }

  return (
    <>
      {isMessageOpen && (
        <RollingMessageModal
          rollingMessageData={modalCardData}
          setIsOpen={setIsMessageOpen}
        />
      )}
      {cardData &&
        cardData.map((el) => (
          <S.CardItem key={el.id} onClick={() => handleMessageModalOpen(el)}>
            <S.CardHeader>
              <S.ProfileImage
                src={el.profileImageURL}
                alt=""
              ></S.ProfileImage>
              <S.CardHeaderContainer>
                <S.CardHeaderName>
                  <S.From>From.</S.From>
                  <S.Name>{el.sender}</S.Name>
                </S.CardHeaderName>
                <Relationship $state={el.relationship}>
                  {el.relationship}
                </Relationship>
              </S.CardHeaderContainer>
              {currentURL.includes("edit") &&
                (localStorage.getItem(`${el.id}-Message`) === "Owner" ||
                  isPageOwner) && (
                  <TrashButton
                    type="button"
                    onDeleteItem={onDeleteItem}
                    id={el.id}
                  />
                )}
            </S.CardHeader>
            <S.Content fontFamily={el.font}>{el.content}</S.Content>
            <S.Date className="font-12-regular">
              {formatDate(el.createdAt)}
            </S.Date>
          </S.CardItem>
        ))}
      {!cardLoaded && (
        <>
          {Array.from({ length: 8 }).map((_, i) => (
            <SkeletonUI
              key={i}
              width="38rem"
              height="28rem"
              mWidth="100%"
              mWeight="100%"
              radius="1.6rem"
              position="relative"
            />
          ))}
        </>
      )}
      <S.ContentEndPoint ref={ref}></S.ContentEndPoint>
    </>
  );
}
